@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --first-color: #fcffe7;
  --secondary-color: #bad7e9;
  --text-color: #2b3467;
  --secondary-text-color: #eb455f;
  --shadow: 0px 5px 3px rgba(170, 170, 170, 0.4);
  --font-family: 'Inter', sans-serif;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  font-family: var(--font-family);
  color: #2b3467;
  color: var(--text-color);
  background-color: #fcffe7;
  background-color: var(--first-color);
}

hr {
  margin: 2rem 0;
  border: 0;
  border-top: 1px solid #ccc;
}

main {
  height: 100vh;
  display: grid;
  grid-template-columns: 300px auto;
}

h1,
h2,
h3 {
  margin: 0.3rem 0;
}

#form {
  background-color: white;
  padding: 2rem;
  border-radius: 3px;
  box-shadow: 0px 5px 3px rgba(170, 170, 170, 0.4);
  box-shadow: var(--shadow);
  max-width: 800px;
}

/* Animation */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes fadein {
  from {
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
/* /Animation */

#form h1 {
  font-size: 2rem;
  margin-top: 0;
  margin-bottom: 0.8rem;
  text-transform: capitalize;
}

#logo {
  color: #fcffe7;
  color: var(--first-color);
  margin-bottom: 1.5rem;
}

#side-menu {
  font-size: 1rem;
  padding: 1.2rem;
  background-color: #2b3467;
  background-color: var(--text-color);
}

#side-menu ul {
  list-style: none;
}

#side-menu ul li {
  /* border-bottom: 1px solid var(--secondary-text-color); */
  padding: 0.8rem 0;
}

#side-menu a {
  color: #bad7e9;
  color: var(--secondary-color);
  text-decoration: none;
  display: block;
  transition: 0.3s;
}

#side-menu a:hover {
  color: white;
}

#side-menu a.active {
  color: white;
}

#main-content {
  padding: 2rem;
  overflow: auto;
  -webkit-animation: fadein 0.3s;
          animation: fadein 0.3s;
  position: relative;
}

#main-content .account {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  padding: 1.5rem;
  border-radius: 0.3rem;
  margin-bottom: 1rem;
  box-shadow: 0px 5px 3px rgba(170, 170, 170, 0.4);
  box-shadow: var(--shadow);
}

#main-content h3 {
  font-weight: normal;
  text-transform: uppercase;
}

#main-content .balance {
  font-weight: 800;
  font-size: 2rem;
}

#main-content .details {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

#actions {
  margin-top: 1rem;
}

#actions button {
  background-color: #fcffe7;
  background-color: var(--first-color);
  color: #eb455f;
  color: var(--secondary-text-color);
  padding: 0.5rem;
  font-size: 1.1rem;
  border: 0;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 3px;
  margin-right: 0.3rem;
}

#actions button:hover {
  background-color: rgba(219, 204, 163, 0.3);
  color: #2b3467;
  color: var(--text-color);
}

#transactions {
  padding: 2rem;
  border-radius: 3px;
  background-color: white;
  box-shadow: 0px 5px 3px rgba(170, 170, 170, 0.4);
  box-shadow: var(--shadow);
}

#transaction-div {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}

.transaction-item {
  display: grid;
  grid-template-columns: 200px auto 100px;
  padding: 0.8rem;
}

#transactions table {
  border-collapse: collapse;
  table-layout: auto;
  width: 100%;
}

#transactions th {
  padding-bottom: 1rem;
  font-weight: normal;
}

#transactions table th,
td {
  border-bottom: 1px solid rgba(59, 49, 40, 0.2);
}

#transactions table tr {
}

#transactions table td:nth-child(1),
#transactions table td:nth-child(2),
#transactions th td:nth-child(2) {
  text-align: left;
  padding: 0.8rem;
}

#transactions table td:nth-child(3),
#transactions table td:nth-child(4) {
  text-align: right;
  padding: 0.8rem;
}

#login-page {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

label {
  width: 100%;
  display: block;
}
#login {
  background-color: rgb(217, 224, 238);
  max-width: 500px;
  width: 100%;
  padding: 2rem;
  border-radius: 3px;
  box-shadow: 0px 5px 3px rgba(170, 170, 170, 0.4);
  box-shadow: var(--shadow);
}

#login #logo {
  color: #2b3467;
  color: var(--text-color);
  font-size: 2.8rem;
  text-align: center;
}

#login label,
label {
  text-transform: uppercase;
  margin-bottom: 0.3rem;
  margin-top: 0.6rem;
  color: rgba(55, 72, 72, 0.618);
}

#login input,
input,
select,
textarea {
  font-family: 'Inter', sans-serif;
  font-family: var(--font-family);
  outline: none;
  width: 100%;
  font-size: 1.4rem;
  padding: 0.3rem 0.5rem;
  border-radius: 3px;
  border: 1px solid #2b3467;
  border: 1px solid var(--text-color);
}

.btn,
.btn2 {
  background-color: #eb455f;
  background-color: var(--secondary-text-color);
  color: white;
  padding: 0.7rem;
  font-size: 1.3rem;
  text-transform: uppercase;
  border: 0;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 3px;
  margin: 1rem 0 0 0;
  margin-bottom: 1rem;
  display: block;
  width: 100%;
}

.btn2 {
  display: inline-block;
  width: auto;
}

.btn:hover,
.btn2:hover {
  background-color: #2b3467;
  background-color: var(--text-color);
  color: white;
}

.notif {
  display: block;
  width: 100%;
  text-align: center;
  padding: 0.5rem;
  border-radius: 3px;
}

.danger {
  color: red;
  background-color: rgba(255, 0, 0, 0.1);
}

.success {
  color: green;
  background-color: rgba(0, 128, 0, 0.1);
}

input:disabled {
  border: 1px solid #ccc;
}

.right {
  text-align: right;
}

.left {
  text-align: left;
  padding-left: 0;
}

.transfer-icon {
  text-align: center;
  font-size: 3rem;
  margin-top: 1rem;
}

.big-input {
  font-size: 2rem;
  font-weight: 800;
}

.main {
  font-size: 2rem;
  margin-bottom: 1rem;
}

#form.budget {
  width: 100%;
  max-width: 1280px;
}

.budget-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}
.overlay {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(126, 126, 126, 0.5);
}
.modal {
  -webkit-animation: fadein 0.3s;
          animation: fadein 0.3s;
  background-color: white;
  padding: 2rem;
  border-radius: 0.5rem;
  box-shadow: 0px 5px 3px rgba(170, 170, 170, 0.4);
  box-shadow: var(--shadow);
  width: 500px;
  position: absolute;
  left: calc(50% - 250px);
  top: calc(40% - 250px);
  text-align: center;
}

.modal button {
  margin: 0.3rem;
  margin-top: 1.3rem;
}

.modal label {
  text-align: left;
}

.btn-muted {
  background-color: rgba(219, 204, 163, 0.3);
  color: #eb455f;
  color: var(--secondary-text-color);
}

.modal input[type='number'] {
  text-align: right;
}

.modal textarea {
  height: 100px;
}

.modal h2 {
  margin-bottom: 1rem;
}

.budget-row {
  padding: 0.8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.odd {
}

.even {
  background-color: rgba(238, 238, 238, 0.5);
}

.budget-title {
  display: inline-block;
  width: 100%;
}

.budget-title span {
  cursor: pointer;
}

.mr {
  margin-right: 0.7rem;
  font-size: 1.3rem;
  background-color: rgba(219, 204, 163, 0.4);
  color: #2b3467;
  color: var(--text-color);
  border: none;
  padding: 0.1rem 0.3rem;
  padding-top: 0.4rem;
  border-radius: 5px;
  font-weight: 800;
}

.mr:hover {
  background-color: rgba(255, 0, 0, 0.2);
  color: red;
  cursor: pointer;
}

.title {
  text-transform: capitalize;
}

